import * as React from 'react';

import {Code, SideSheet} from '../arroz-con-webo';

const TestPage = () => {
  const [open, set_open] = React.useState(false);

  return (
    <div style={{'display': 'flex', 'height': '100vh'}}>
      <SideSheet open={open}>
        <Code rounded outlined language="html">
{`<html>
  <head>
  </head>
  <body>
  </body>
</html>`}
        </Code>
      </SideSheet>
      <button onClick={() => set_open(!open)}></button>
      <Code rounded outlined language="cpp" showLineNumbers>
{`#include <stdlib.h>

// This is a test
int main() {
  string car = "Ford Mustang";

  return 0;
}`}
      </Code>
    </div>
  );
}
  
export default TestPage;
  
export const Head = () => <title>Test Page</title>;